






















import { Component, Vue, Watch } from 'vue-property-decorator'
import { ISlot } from '../model/ISlot'
import { EObjects, IObject } from '../model/IObject'
import Zone from './objects/Zone.vue'
import { mapState } from 'vuex'

@Component({
  components: {
    Zone,
  },
  computed: {
    ...mapState([
      'objects',
    ]),
  },
})
export default class Schema extends Vue {

  private colorBackground: string = '#ffffff'
  private colorGrid: string = '#cccccc'
  private toolsWrapHeight = 40
  private gridSize = 10

  private objects?: Array<IObject>

  private width: number = 0
  private height: number = 0

  private emptySlot: ISlot = {
    objectId: 0,
    destination: false,
    source: false
  }
  private emptyZone: IObject = {
    id: 0,
    type: EObjects.zone,
    selected: true,
    slots: new Array(18).fill(this.emptySlot)
  }

  // ! demonstration purpose only
  // @Watch('objects', { immediate: false, deep: true })
  // objectWatch(value: any) { console.log(value) }
 
  init(): void {
    this.width = 0
    this.height = 0
    const svgWrapper: HTMLElement | null = document.getElementById('schema')
    if (!svgWrapper) return
    this.width = svgWrapper.clientWidth
    this.height = svgWrapper.clientHeight - this.toolsWrapHeight
  }

  addObjectZone(): void {
    this.$store.commit('addObjectZone', this.emptyZone)
  }

  templateGrid(): string {
    let template = ``
    if (this.width < this.gridSize && this.height < this.gridSize) return template
    for (let i: number = 0; i < this.width; i += this.gridSize) {
      template = `${template}
      <line x1="${i}" y1="0" x2="${i}" y2="${this.height}"
        stroke-width="0.5" stroke="${this.colorGrid}" />`
    }
    for (let i: number = 0; i < this.height; i += this.gridSize) {
      template = `${template}
      <line x1="0" y1="${i}" x2="${this.width}" y2="${i}"
        stroke-width="0.5" stroke="${this.colorGrid}" />`
    }
    return template
  }

  zoneList(): Array<IObject> {
    return this.objects ? this.objects.filter((item: IObject) => item.type === EObjects.zone) : []
  }

  omClick() {
    this.$store.commit('selectObject', 0)
  }

  mounted(): void {
    this.init()
    window.addEventListener('resize', this.init)
    window.addEventListener('mousedown', this.omClick)
  }

  beforeDestroy(): void {
    window.removeEventListener('resize', this.init)
    window.removeEventListener('mousedown', this.omClick)
  }

}
