













import { Component, Prop, Vue } from 'vue-property-decorator'
import { ICoordinates } from '../../model/ICoordinates'
import Svg from '../Svg.vue'

@Component({})
export default class Zone extends Vue {

  @Prop() private id?: number
  @Prop() private selected?: boolean
  @Prop() private slots?: Array<number>

  private width: number = 250
  private height: number = 200

  private colorBackground: string = '#cccccc'
  private colorBorder: string = '#999999'

  private gridSize = 10

  private elementSelf?: SVGElement | null
  private offset?: ICoordinates
  private isDrag: boolean = false

  startDrag(event: any): void {
    this.$store.commit('toFrontObject', this.id)
    this.$store.commit('selectObject', this.id)
    for (const index in event.path) {
      const element = event.path[index]
      if (element.getAttribute && element.getAttribute('id') === `drag-${this.id}`) {
        this.isDrag = true
        this.offset = !this.offset ? this.getMousePosition(event) : this.offset
        break
      }
    }
    event.stopImmediatePropagation()
  }

  drag(event: any): void {
    if (!this.isDrag) return
    event.preventDefault()
    const coord = this.getMousePosition(event)
    const x = Math.floor((coord.x - this.offset.x) / this.gridSize) * this.gridSize
    const y = Math.floor((coord.y - this.offset.y) / this.gridSize) * this.gridSize
    if (x && y) this.elementSelf.setAttribute('transform', `translate(${x},${y})`)
  }

  endDrag(event: any): void {
    this.isDrag = false
  }

  getMousePosition(event: any) : ICoordinates {
    return {
      x: event.clientX,
      y: event.clientY,
    }
  }

  mounted(): void {
    this.elementSelf = document.getElementById(`zone-${this.id}`)
    if (this.elementSelf) {
      this.elementSelf.addEventListener('mousedown', this.startDrag)
      this.elementSelf.addEventListener('mousemove', this.drag)
      this.elementSelf.addEventListener('mouseup', this.endDrag)
      this.elementSelf.addEventListener('mouseleave', this.endDrag)
    }
  }

  beforeDestroy(): void {
    if (this.elementSelf) {
      this.elementSelf.removeEventListener('mousedown', this.startDrag)
      this.elementSelf.removeEventListener('mousemove', this.drag)
      this.elementSelf.removeEventListener('mouseup', this.endDrag)
      this.elementSelf.removeEventListener('mouseleave', this.endDrag)
    }
  }

}
