import Vue from 'vue'
import Vuex from 'vuex'

import { IObject } from '../model/IObject'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lastId: 0,
    objects: Array<IObject>(),
  },
  mutations: {
    addObjectZone(state, zone: IObject): void {
      state.lastId++
      zone.id = state.lastId
      state.objects.push({...zone})
    },
    selectObject(state, id: number) {
      for (const item of state.objects) {
        item.selected = (item.id === id) // mutation right here
      }
    },
    toFrontObject(state, id: number) {
      const objects: Array<IObject> = []
      let object: IObject | null = null
      for (const item of state.objects) {
        if (item.id === id) {
          object = item
        } else {
          objects.push(item)
        }
      }
      if (object !== null) objects.push(object)
      state.objects = objects
    }
  },
  actions: {
  },
  modules: {
  }
})
